@import 'variables';

.header{
    position: relative;
    top: 0vh;
    padding-top: 10vh;
    background-color: black;
    height: 90vh;

    &__title{
        position: absolute;
        color: white;
        background-color: rgba($color: #000000, $alpha: .2);
        letter-spacing: 2px;
        padding-top: 10px;
        top: 30%;
        width: 100%;
        z-index: 1;
        padding-left: 5%;
        font-family: $menu-font;
        font-size: 2rem;
        line-height: 1.5;

        @media (min-width:640px){
            font-size: 2.4rem;
        }

        @media (min-width: 768px){
            font-size: 2.6rem;
        }

        @media (min-width: 1024px){
            font-size: 2.8rem;
            padding-left: 15%;
        }

        @media (min-width: 1440px){
            font-size: 3.2rem;
        }
    }

    &__btn{
        position: absolute;
        bottom: 20%;
        z-index: 1;
        left: 5%;
        font-family: $menu-font;
        background-color: rgba($color: #000000, $alpha: .2);
        outline: none;
        border: 1px solid white;
        font-size: 1.7rem;

        @media (min-width:1024px){
            font-size: 2rem;
            bottom: 10%;
            left: 15%;
        }

        &:hover{
            background-color: white;

            a{
                color: black
            }
        }
      

        a{
            text-decoration: none;
            display: block;
            color: white;
            padding: 10px;
        }
    }

    img{
        height: 90vh;
        width: 100%;
        object-fit: cover;
    }
}

// splide styles
.splide__pagination__page {
    background-color: #444;
    width: 10px;
    height: 10px;
    border: 1px solid white;

    &.is-active{
        background-color: white;
        width: 15px;
        height: 15px;
    }
  }